@font-face {
  font-family: Roboto;
  font-display: swap;
  src: url("./fonts/Roboto-Medium.woff2") format("woff2"), url("./fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  font-display: swap;
  src: url("./fonts/Roboto-Regular.woff2") format("woff2"), url("./fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-size: 0;
  line-height: 0;
  border: 0;
  outline: none;
}

html, body {
  height: 100%;
  font-family: "Roboto", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
}
main {
  flex: 1 0 auto;
}

header {
  height: 80px;
  background: #090808;
}

footer {
  flex: 0 0 auto;
  height: 160px;
  background: #737373;
}

.container {
  margin: 80px 160px;
  display: flex;
}

.output-wrapper {
  flex: 1 0 auto;
}

h1 {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 72px;
  line-height: 84px;
  color: #000000;
}

p, li {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 38px;
  color: #000000;
}

.list-title {
  margin-top: 38px;
  margin-bottom: 8px;
}

ul {
  list-style: none;
}

form {
  width: 400px;
  border: 1px solid #000000;
  padding: 32px 16px;
  margin-top: 80px;
}

label {
  display: block;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

input {
  display: block;
  width: 100%;
  border: 1px solid #000000;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  margin-bottom: 16px;
  padding: 14px 11px 15px;
}

input[type="button"] {
  margin-bottom: 0;
  background: #000000;
  color: #FFFFFF;
}